body { background: var(--bg-gray-50); margin: 0; font-family: inherit; }
.App {
  min-height: 100vh;
  padding: 1.5rem 2vw;
}
.project-grid {
  display: flex;
  flex-direction: column;
}
.card {
  background: var(--bg-white);
  border: 1px solid var(--border-light);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-sm);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-lg);
  display: flex;
  flex-direction: column;
  min-width: 200px;
}
.skill-badge {
  background: #e0edfa;
  color: #2563eb;
  font-size: 0.85rem;
  border-radius: var(--radius-md);
  padding: 0.2em 0.7em;
  margin-right: 4px;
  margin-bottom: 4px;
}
.loading-spinner {
  text-align: center;
  color: var(--text-muted);
  padding: 2rem 0;
}
.error-box {
  background: #fee2e2;
  border: 1px solid #fca5a5;
  color: #b91c1c;
  padding: var(--spacing-md);
  border-radius: var(--radius-md);
  margin-bottom: var(--spacing-md);
  text-align: center;
}
